  import Post from './Post.vue'
  import { mapGetters} from 'vuex'
  export default{
  components: {
    'app-post': Post
  },
  computed: {
    ...mapGetters('postsModule',['posts'])
  },
  methods:{
    loadPosts () {

      let categoryId = 2
      if(this.$route.params.id === 'mobile') {
          categoryId = 11
      }
      this.$store.dispatch('postsModule/updateCategory', categoryId)

    }
  },
  watch:{
      '$route' (to, from) {
        this.loadPosts()
      }
    },
  created () {
    this.loadPosts()
  }
}



